import "./App.css";
import { useEffect, useState } from "react";
import { detectEyeClosure, setup } from "./tf";
import { Search } from "./Search";

function App() {
  const [eyesClosed, setEyesClosed] = useState(false);
  const [d, setD] = useState({});
  const [nFound, setNotFound] = useState("loading");
  const [detector, setDetector] = useState("loading detector");
  useEffect(() => {
    let intervalId;
    setup().then(
      () =>
        (intervalId = setInterval(() => {
          detectEyeClosure().then((stuff = {}) => {
            const { closed, leftEAR, rightEAR, notFound, detector } = stuff;
            setNotFound(notFound);
            setEyesClosed(closed);
            setD({ leftEAR, rightEAR });
            console.log("detector", detector);
            setDetector(detector);
          });
        }, 1000 / 5))
    );
    return () => clearInterval(intervalId);
  }, []);
  return (
    <div className="App">
      {d.leftEAR}
      {nFound}
      {detector}
      <Search eyesClosed={eyesClosed} />
    </div>
  );
}

export default App;
